<template>
  <div class="result-list center-center">
    <el-button type="primary" :loading="true"
      >正在验证支付结果，请勿关闭页面</el-button
    >
    <el-dialog
      title="复制保存好设备id，丢失将无法找回！！"
      :modal-append-to-body="true"
      :append-to-body="true"
      :visible.sync="copyVisible"
      :close-on-click-modal="false"
      :width="isMobile ? '98%' : '40%'"
      class="buy-vip-dialog"
    >
      <div>
        <el-form status-icon ref="ruleForm" class="demo-ruleForm">
          <el-form-item class="check-vip">
            <el-input v-model.trim="site.ip"></el-input>
            <el-button
              type="primary"
              v-clipboard:copy="site.ip"
              v-clipboard:success="onCopy"
              v-clipboard:error="onError"
              >复制</el-button
            >
          </el-form-item>
        </el-form>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import apiUser from "@/api/user";
import auth from "@/plugins/auth";
export default {
  name: "ResultView",
  data() {
    return {
      show: false,
      copyVisible: false,
      out_trade_no: this.$route.query.out_trade_no,
    };
  },
  computed: {
    site() {
      return this.$store.state.site;
    },
    orders() {
      return this.$store.state.orders;
    },
    isMobile() {
      return this.$store.state.isMobile;
    },
  },
  methods: {
    sleep(t) {
      return new Promise((resolve) => {
        setTimeout(() => {
          resolve();
        }, 1000 * t);
      });
    },
    onCopy(e) {
      this.$message({
        message: `复制成功：${e.text}`,
        type: "success",
      });
      this.copyVisible = false;
      const buyId = auth.getToken("buyId");
      if (buyId) {
        this.$router.push(`/javs/${buyId}.html`);
      }
    },
    onError() {
      this.$message({
        message: `复制失败`,
        type: "error",
      });
    },
    async queryApplyInfo() {
      const out_trade_no = this.out_trade_no;
      if (!out_trade_no) {
        this.$router.push(`/`);
        return;
      }
      const [err, res] = await apiUser.vaidOrder({
        out_trade_no,
      });
      if (err) {
        this.$router.push(`/`);
        return;
      }
      const { result } = res;
      this.$store.commit("setOrders", result);
      this.copyVisible = true;
      this.$message({
        message: `您已开通VIP，可观看所有视频`,
        type: "success",
      });
      this.$router.push(`/`);
    },
  },
  async created() {
    await this.sleep(4);
    this.queryApplyInfo();
  },
};
</script>
<style scoped lang="scss">
.page-list {
  ::v-deep {
    .el-pager > li:last-child {
      display: none;
    }
  }
}
</style>
